////
/// Statistics block
/// @group patterns
////
.ksp-block {
    background: $color-purewhite;

    // >= Tablet
    @include media('>=tablet') {
        @include standard-grid;
        padding: $measure--basic 0;
        .ksp-block__heading {
            grid-column: 3/-3;
        }
        .ksp__items {
            display: flex;
        }
    }

    // < Tablet
    @include media('<tablet') {
        padding: 0 $measure--small2 $measure--basic $measure--small2;
        margin-top: 0;

        &[data-slider-render='tablet'] {
            .slider-block--mobile {
                [data-visible='false'] {
                    display: none;
                }
                .slider-block__control {
                    display: flex;
                    .slider-block__control__buttons--dot {
                        display: block;
                    }
                }
            }
        }

        .ksp-block__heading,
        .ksp-block__introduction,
        .cta-block {
            text-align: center;
        }

        .ksp-block__heading {
            padding: $measure--basic;
            text-align: center;
        }
    }

    // Generic
    .ksp-block__heading,
    .ksp-block__introduction,
    .cta-block {
        grid-column: 2/-2;
        margin-left: auto;
        margin-right: auto;
    }
    
    .ksp__items {
        grid-column: 1/-1;
        margin: $measure--basic auto;
        max-width: none;
        text-align: center;

        .ksp__item {
            list-style-type: none;
            padding-left: $measure--basic;
            padding-right: $measure--basic;

            h2 {
                @include h1style();
                font-weight: $font-weight-light;
                .animate--number__unit--post {
                    @include alphatype();
                    margin-left: $measure--quarter;
                }
            }
        }

        .ksp__item__description p {
            margin-left: auto;
            margin-right: auto;
        }

        .ksp__item__overline {
            @include font-family('default');
            color: $color-gray7;
            font-size: $font-size--alpha;
            font-weight: $font-weight-book;
        }

        .ksp__item__pre-figure,
        .ksp__item__figure,
        .ksp__item__post-figure,
        .ksp__item__text-only {
            @include font-family('heading');
            font-weight: $font-weight-light;
            display: inline;
            color: $color-red50;
        }

        .ksp__item__text {
            @include font-family('default');
            color: $color-gray10;
            font-size: $font-size--alpha;
            font-weight: $font-weight-text;
            margin: $measure--basic 0;
        }

        .ksp__item__icon {
            display: block;
            margin: 0 auto;
            width: 52px; // Icon height matches text height.

            @include media('<tablet') {
                margin: 0;
                display: inline;
            }
        }

        .ksp__item__heading--top-padding,
        .ksp__item__icon--top-padding {
            @include media('<tablet') {
                padding-top: $measure--basic;
            }
        }

        // Animation
        .animation-incomplete {
            opacity: 0;
        }

        .animation-incomplete.animation-complete {
            opacity: 1;
            transition: opacity 1500ms;
        }

        .animate--number--init {
            .animate--number__unit {
                opacity: 0;
            }
        }

        .animate--number--complete {
            .animate--number__unit {
                transition: opacity 1s;
                opacity: 1;
            }
        }
    }
}